@import "../../styles/variables/variables.scss";

@mixin center() {
  display: flex;
  align-items: center;
  justify-content: center;
}

.start__container {
  width: 100%;
  height: 100vh;
  @include center();
  flex-direction: column;
  padding: 2rem 0;

  header {
    font-family: $Fredoka;
    flex-direction: column;
    @include center();

    h1 {
        font-size: 2rem;
    }
  }

  main {
    max-width: 350px;
    @include center();
    flex-direction: column;
    padding: 1rem;
    gap: 1rem;
    picture {
      width: 100%;
      @include center();
      svg {
        height: 200px;
        max-width: 100%;
      }
    }

    

    .title {
      text-align: center;

    }

    .btn__group {
        @include center();  
        flex-direction: column;
        gap: .5rem;
        padding-top: 1rem;
        width: 100%;

        button {
            width: 100%;
            height: 40px;
            border: none;
            border: 1px solid $primary;
            border-radius: 5px;
            cursor: pointer;
        }

        button:nth-child(1) {
            background-color: #0090ff;
            color: $neutral;
            border: 1px solid #0090ff;
        }
    }
  }
}

@media screen and (max-width: 425px) {
    .title {
        max-width: 100%;
    }
}
