@import "../../styles/variables/variables.scss";

@mixin center() {
  display: flex;
  align-items: center;
  justify-content: center;
}

.entry__container {
  width: 100%;
  height: 100vh;
  @include center();
  flex-direction: column;

  header {
    @include center();
    padding: 2rem;
    h1 {
      font-family: $Fredoka;
    }
  }

  .entry__width {
    width: 100%;
  }

  .entry {
    text-align: center;
    flex-direction: column;
    padding: 1rem;
    @include center();
    max-width: 350px;
    gap: 1rem;

    &__head {
      text-align: start;
      width: 100%;
    }

    &__form {
      @include center();
      flex-direction: column;
      gap: 0.5rem;
      width: 100%;
      text-align: start;

      .pass {
        color: red;
        font-size: 14px;
        line-height: 1rem;
      }

      button {
        width: 100%;
        height: 40px;
        border: none;
        background-color: $primary;
        color: $neutral;
        border-radius: 5px;
        cursor: pointer;
        margin-top: 0.5rem;
      }
    }

    &__group {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 0 0.5rem;
      border: 1px solid $primary;
      border-radius: 5px;
      

      input {
        width: 100%;
        height: 40px;
        font-size: 15px;
        outline: none;
        background: none;
        border: none;
      }

      svg {
        width: 20px;
        height: 20px;
      }
      .icon {
        @include center();
        cursor: pointer;
      }

      .check {
        color: green;
      }

      .close {
        color: red;
      }

      .verify__icon {
        @include center();
        display: none;
      }

      .verify__icon.active {
        display: flex;
      }
    }

    &__socials {
      width: 100%;
      display: grid;

      .social__group {
        padding-top: 1rem;
        width: 100%;
      }

      .social {
        @include center();
        gap: 1rem;
        width: 100%;
        height: 40px;
        border: 1px solid $primary;
        border-radius: 5px;
        cursor: pointer;
        margin-bottom: 0.5rem;
        
      }

      a {
        text-decoration: none;
      }
    }
  }
}
